//  CSS  
import '@/styles/global.css'

/**
 * gatsby-browser        
 *      ,   ,     .
 */
export const onInitialClientRender = () => {
  window.fbAsyncInit = () => {
    window.FB.init({
      appId: process.env.GATSBY_FACEBOOK_APP_ID,
      xfbml: true,
      version: 'v16.0',
    })
    window.FB.AppEvents.logPageView()
  }

  const loadSDK = (d: Document, s: string, id: string) => {
    const fjs = d.getElementsByTagName(s)[0]
    if (d.getElementById(id)) {
      return
    }
    const js = d.createElement(s) as HTMLScriptElement
    js.id = id
    js.src = 'https://connect.facebook.net/en_US/sdk.js'
    fjs.parentNode?.insertBefore(js, fjs)
  }

  loadSDK(document, 'script', 'facebook-jssdk')
}

export { wrapRootElement } from './src/components'

export { wrapPageElement } from './src/components'
